import React, { useState, useEffect } from "react";
import Form from "@rjsf/core";
import validator from "@rjsf/validator-ajv8";
import axios from "axios";
import "./NhapData.css"; // Import file CSS ty chnh

const schema = {
  title: "NHP THNG TIN IU KHIN H THNG",
  type: "object",
  required: ["desire", "k_percent"],
  properties: {
    desire: { type: "number", title: "Nhp khong cch mong mun (mm)" },
    k_percent: { type: "number", title: "Nhp gi tr phn trm (K %)" },
  },
};

const TimeDisplay = () => {
  const [time, setCurrentTime] = useState(new Date());

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <div>
      <p style={{ fontSize: "1.2em" }}>
        Thi gian hin ti: {time.toLocaleTimeString()}
      </p>
    </div>
  );
};

const UserLogin = () => {
  const [formData, setFormData] = useState({});

  const handleSubmit = async ({ formData }) => {
    try {
      const dataWithTime = {
        ...formData,
        time: new Date().toISOString(),
      };

      console.log("D liu gi i:", dataWithTime);

      // Gi POST request n endpoint th nht
      const response = await axios.post(
        "https://us-east-1.aws.data.mongodb-api.com/app/agg_func-voayj/endpoint/update_React",
        dataWithTime
      );

      console.log("Kt qu t server:", response.data);

      setFormData({});
    } catch (error) {
      console.error("Li khi gi d liu:", error);
    }
  };

  return (
    <div className="auth-form-container">
      <Form
        schema={schema}
        validator={validator}
        formData={formData}
        onChange={({ formData }) => setFormData(formData)}
        onSubmit={handleSubmit}
      />
      <TimeDisplay />
    </div>
  );
};

export default UserLogin;
